<template>
  <div class='hello'>
    <h1>{{ msg }}</h1>
    <h2>Essential Links</h2>
    <ol>
      <li v-for='site in sites' :key="site.name">
      {{ site.name }}
      </li>
    </ol>
    {{myList}}
  </div>
</template>

<script>
export default {
  name: 'a',
  data () {
    return {
      msg: 'data',
      error: 'Kepa',
      sites: [{ name: 1 }, { name: 2 }, { name: 3 }, { name: 4 }, { name: 5 }]
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



